// вспомогателный модуль
@desktop-width: 1160px;
@mobile-width-only: 1160px;

ul, 
li {
    list-style: none;
    padding: 0;
    margin: 0;
}
body {
    background: #F3F7FA;;
    margin: 0;
    padding: 0;
    font-family: 'Open Sans', sans-serif;


    .visually-hidden:not(:focus):not(:active),
    input[type="checkbox"].visually-hidden,
    input[type="radio"].visually-hidden {
        position: absolute;

        width: 1px;
        height: 1px;
        margin: -1px;
        padding: 0;
        overflow: hidden;

        white-space: nowrap;

        border: 0;

        clip: rect(0 0 0 0);
        clip-path: inset(100%);
    }
}
